export default {
  props: {
    editprofil: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      validRef: false,
      success: false,
      error: null,
      verPhone: '',
      verEmail: '',
      verWeb: '',
      loading: false,
      rules: {
        phone: [
          (v) => !!v || 'Telefonnummer nicht vergessen',
          (v) =>
            /^(?:\+\d{2}|0|00\d{2})(?:\s*\d{3}){2}\s*\d{4,10}/.test(v) ||
            'Ungültiges Format',
        ],
        email: [
          (v) =>
            /[^@ \t\r\n]+@[^@ \t\r\n]+\.[^@ \t\r\n]+/.test(v) ||
            'Ungültiges Format',
        ],
      },
    }
  },
  computed: {
    verified() {
      return this.$store.getters['modules/user/verified']
    },
    isVerifying() {
      return this.$store.getters['modules/user/isVerifying']
    },
  },
  methods: {
    verifyProfil() {
      this.loading = true
      this.$store
        .dispatch('modules/user/requestVerify', {
          tel: this.verPhone,
          www: this.verWeb,
          email: this.verEmail,
        })
        .then(() => {
          this.loading = false
          this.success = true
        })
    },
  },
}
